import React from 'react';
import './Card.css';

const Card = (props) => {
	return (
		<div className="cardContainer">
			<img src={`https://robohash.org/${props.monster.id}?set=set2&size=180x180`} alt="monster" />
			<h1>{props.monster.name}</h1>
			<p>{props.monster.email}</p>
		</div>
	);
};

export default Card;
